import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styles from '../commonStyles/formComStyles.module.css';
import { operations } from '../../redux/contacts';
import Button from '../Button/Button';

class ContactForm extends Component {
  static propTypes = {
    handleSubmit: PropTypes.func,
    addContact: PropTypes.func,
  };

  state = {
    name: '',
    number: '',
  };

  handleChange = event => {
    this.setState({ [event.currentTarget.name]: event.currentTarget.value });
  };

  handleSubmit = event => {
    event.preventDefault();

    const newContact = {
      // id: v4(),
      name: this.state.name,
      number: this.state.number,
    };

    this.props.addContact(newContact);
    this.setState({ name: '' });
    this.setState({ number: '' });
  };

  render() {
    return (
      <div>
        <form onSubmit={this.handleSubmit} className={styles.form}>
          <label className={styles.label}>
            Name
            <input
              type="text"
              name="name"
              pattern="^[a-zA-ZÐ°-ÑÐ-Ð¯]+(([' -][a-zA-ZÐ°-ÑÐ-Ð¯ ])?[a-zA-ZÐ°-ÑÐ-Ð¯]*)*$"
              title="Ð˜Ð¼Ñ Ð¼Ð¾Ð¶ÐµÑ‚ ÑÐ¾ÑÑ‚Ð¾ÑÑ‚ÑŒ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ð¸Ð· Ð±ÑƒÐºÐ², Ð°Ð¿Ð¾ÑÑ‚Ñ€Ð¾Ñ„Ð°, Ñ‚Ð¸Ñ€Ðµ Ð¸ Ð¿Ñ€Ð¾Ð±ÐµÐ»Ð¾Ð². ÐÐ°Ð¿Ñ€Ð¸Ð¼ÐµÑ€ Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan Ð¸ Ñ‚. Ð¿."
              required
              value={this.state.name}
              onChange={this.handleChange}
              className={styles.input}
            />
          </label>
          <label className={styles.label}>
            Phone number
            <input
              type="tel"
              name="number"
              pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
              title="ÐÐ¾Ð¼ÐµÑ€ Ñ‚ÐµÐ»ÐµÑ„Ð¾Ð½Ð° Ð´Ð¾Ð»Ð¶ÐµÐ½ ÑÐ¾ÑÑ‚Ð¾ÑÑ‚ÑŒ Ñ†Ð¸Ñ„Ñ€ Ð¸ Ð¼Ð¾Ð¶ÐµÑ‚ ÑÐ¾Ð´ÐµÑ€Ð¶Ð°Ñ‚ÑŒ Ð¿Ñ€Ð¾Ð±ÐµÐ»Ñ‹, Ñ‚Ð¸Ñ€Ðµ, ÐºÑ€ÑƒÐ³Ð»Ñ‹Ðµ ÑÐºÐ¾Ð±ÐºÐ¸ Ð¸ Ð¼Ð¾Ð¶ÐµÑ‚ Ð½Ð°Ñ‡Ð¸Ð½Ð°Ñ‚ÑŒÑÑ Ñ +"
              required
              value={this.state.number}
              onChange={this.handleChange}
              className={styles.input}
            />
          </label>
          <Button
            type="submit"
            title="Add contact &nbsp; âœ…"
            className={styles.button}
          />
        </form>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => {
  return {
    addContact: newContact => dispatch(operations.addContact(newContact)),
  };
};

export default connect(null, mapDispatchToProps)(ContactForm);
